
html, body {
   width: 100%;
  height: 100%;
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  height: calc(100% - 60px);
  width: 100%;
}

#content {
  margin: 100px auto 0 auto;
  width: 70%;
  height: calc(100% - 100px);
}
#title {
  font-size: 100px;
  text-align: center;
  color: #4285F8;
}
#input_text {
  height: 40px;
  width: 90%;
  margin: 30px 5% 0 5%;
}
#list {
  margin: 70px auto;
  height: calc(100% - 345px);
  width: 100%;
  position: relative;
}
#todolist, #finishedlist {
  padding:0;
  width: 40%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  border: #4285F8 2px solid;
}
#todolist {
  position: absolute;
  margin-left: 8%;
  margin-right: 2%;
}
#finishedlist {
  position: absolute;
  margin-left: 52%;
}
.list_name {
  margin: 20px auto 0 auto;
  font-size: 30px;
  font-weight: bolder;
  color: #E6A98A;
}
ul, li {
  position: relative;
  margin: auto;
  text-align: left;
}
ul {
  list-style-position: outside;
  padding:10px 20px 10px 40px;
  width: calc(100%-60px);
  height: calc(100%-20px);
}
li {
  width: 100%;
  height: 40px;
  color:gray;
  font-weight: bolder;
}
.label {
  height: 30px;
  top: 0;
  left: 0;
  position: absolute;
  font-size: 20px;
  margin: 0;
  width:60%;
  height: 30px;
}
.state {
  position: absolute;
  margin-left: 70%;
  height: 30px;
  width:95px;
  background-color: rgb(240, 149, 104);
  color:#b45424;
  font-weight: bolder;
  border: none;
  border-radius: 5px;
}
